html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

html {
  font-size: 16px; }

body {
  color: #666;
  font-family: Arial, Helvetica, sans-serif; }

a {
  color: #009;
  text-decoration: underline;
  cursor: pointer; }

a:hover {
  color: #006; }

canvas {
  display: block; }

[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important; }

.container {
  width: 100%;
  float: left;
  padding: 5%;
  background: #6e7377; }
  .container .progress {
    height: 40px;
    width: 100%;
    margin-bottom: 20px;
    overflow: hidden;
    background-color: #f5f5f5;
    border-radius: 4px;
    -webkit-box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1); }
    .container .progress .progress-bar {
      float: left;
      height: 100%;
      font-size: 12px;
      line-height: 40px;
      color: #fff;
      text-align: center;
      background-color: #5bc0de;
      -webkit-box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.15);
      box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.15);
      -webkit-transition: width 5s ease;
      -o-transition: width .6s ease;
      transition: width .6s ease; }
    .container .progress .text-span {
      float: left;
      z-index: 99;
      color: #666;
      position: absolute;
      left: 50%;
      line-height: 40px; }
    .container .progress .progress-bar-normal {
      background-color: #5bc0de; }
    .container .progress .progress-bar-red {
      background-color: #d9534f; }
  .container .select-box {
    float: left;
    width: 20%;
    border: 1px solid #ccc;
    font-size: 16px;
    height: 30px; }
  .container .button-div {
    float: left;
    width: 80%;
    text-align: center; }
    .container .button-div .button {
      width: 15%;
      height: 30px;
      background: #fff;
      border: 1px solid #ccc;
      color: #004fb6;
      cursor: pointer;
      margin: 0 10px;
      position: relative;
      text-align: center;
      text-decoration: none; }

@media only screen and (max-width: 768px) {
  .container {
    width: 100%; }
    .container .select-box {
      width: 100%;
      margin: 20px 0px; }
    .container .button-div {
      width: 100%;
      margin: 20px 0px; }
      .container .button-div .button {
        width: 100%;
        margin: 20px 0px; } }

@font-face {
  font-family: "generica";
  font-style: normal;
  font-weight: normal;
  src: url("../assets/fonts/generica.eot?#iefix") format("embedded-opentype"), url("../assets/fonts/generica.woff") format("woff"), url("../assets/fonts/generica.ttf") format("truetype"), url("../assets/fonts/generica.svg#generica") format("svg"); }

.incompatible-browser {
  display: none;
  position: relative;
  width: 100%;
  text-align: center; }

.no-js .incompatible-browser {
  display: block; }

.incompatible .incompatible-browser {
  display: block; }

.loader {
  display: none;
  text-align: center; }

.version {
  position: fixed;
  right: 0;
  bottom: 0;
  max-width: 100%;
  padding: 0.5em;
  background: #eee;
  color: #666;
  font-family: Arial, Helvetica, sans-serif;
  opacity: 0.9;
  z-index: 9999; }
  .version span {
    color: #aaa; }
